<template>
  <div id="all_container">
    <!--头部导航栏-->
    <header><navigation /></header>
    <!--主体-->
    <div class="inner_container">
      <div class="menu"><v-menu /></div>
      <div class="banner"><banner /></div>
      <div class="recommed"><recommed /></div>
    </div>
    <div class="foot_container">
      <div><v-sales /></div>
      <div><phone /></div>
      <div><v-video /></div>
    </div>
    <div><v-foot /></div>
  </div>
</template>

<script>
import navigation from "@/components/navigation";
import menu from "@/components/menu";
import banner from "@/views/banner";
import recommed from "@/views/recommed";
import Flash_sales from "@/views/Flash_sales";
import phone from "@/views/phone";
import video from "@/views/video";
import container_foot from "@/views/container_foot";
export default {
  components: {
    "v-menu": menu,
    navigation,
    banner,
    recommed,
    "v-sales": Flash_sales,
    phone,
    "v-video": video,
    "v-foot": container_foot
  },
  data() {
    return {};
  },
  methods: {}
};
</script>

<style scoped>
.all_container {
  width: 100%;
}
.inner_container {
  width: 90%;
  margin: 0 auto;
}
.foot_container {
  width: 100%;
  background: #f5f5f5;
}
</style>



