<template>
  <div class="menu_container">
    <div class="inner_container">
      <div class="image_box">
        <img src="../assets/img/logo1.png" alt="" class="logo_image1" />
        <img src="../assets/img/logo2.png" alt="" class="logo_image2" />
      </div>
      <div class="Menu_labelling">
        <span class="mi_phone" @mouseenter="enter" @mouseleave="leave">
          小米手机
        </span>
        <span @mouseenter="enter" @mouseleave="leave">Redmi 红米</span>
        <span @mouseenter="enter" @mouseleave="leave">电视</span>
        <span @mouseenter="enter" @mouseleave="leave">笔记本</span>
        <span @mouseenter="enter" @mouseleave="leave">家电</span>
        <span @mouseenter="enter" @mouseleave="leave">路由器</span>
        <span @mouseenter="enter" @mouseleave="leave">智能硬件</span>
        <span>服务</span>
        <span>社区</span>
      </div>
      <div class="input_box">
        <input type="text" class="input" value="你好" />
        <button class="search_btn">
          <i class="iconfont icon-search"></i>
        </button>
      </div>
      <div
        class="phone_detail"
        v-show="phone_cover"
        @mouseenter="enter"
        @mouseleave="leave"
      >
        <div class="phone_detail_inner" v-for="(item, index) in 5" :key="index">
          <img src="../assets/img/phone_image.png" class="phone_image" />
          <div class="phone_des">
            <span>小米10 pro</span><span>4999元起</span><i class="divider"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      phone_cover: false
    };
  },
  methods: {
    enter() {
      this.phone_cover = true;
    },
    leave() {
      this.phone_cover = false;
    }
  }
};
</script>

<style scoped>
.inner_container {
  font-size: 0;
}
.image_box {
  width: 55px;
  height: 55px;
  display: inline-block;
  margin-left: 10px;
  margin-top: 24px;
}
.image_box:hover .logo_image2 {
  opacity: 1;
  transition: opacity 0.5s;
}
.logo_image2 {
  position: absolute;
  top: 64px;
  left: 78px;
  opacity: 0;
  transition: opacity 0.5s;
}
.Menu_labelling {
  display: inline-block;
  position: relative;
  left: 120px;
  bottom: 0px;
  font-size: 0;
}
.Menu_labelling span {
  display: block;
  float: left;
  margin-left: 24px;
  font-size: 16px;
  cursor: pointer;
  z-index: 1;
  height: 40px;
}
.Menu_labelling span:hover {
  color: #ff6700;
}
.input_box {
  opacity: 0.7;
  margin-left: 39px;
  float: right;
  margin-top: 28px;
}
.input {
  width: 224px;
  height: 50px;
  outline-style: none;
  border: 1px #e0e0e0 solid;
  position: relative;
  top: -2px;
  font-size: 14px;
  line-height: 48px;
  color: #a4a4a4;
}
.input_box:hover {
  opacity: 1;
}
.search_btn {
  width: 50px;
  height: 52px;
  outline-style: none;
  border: 1px #e0e0e0 solid;
  position: relative;
  right: 6px;
  cursor: pointer;
}
.search_btn i {
  font-size: 20px;
}
/*小米手机下拉菜单样式 */

.phone_detail {
  width: 90%;
  height: 230px;
  animation: run 0.5s;
  box-shadow: 0px 10px 10px #eeeeee; /*下边阴影*/
  display: flex;
  justify-content: space-between;
  position: absolute;
  background: white;
  z-index: 99;
}
.phone_detail_inner {
  width: 160px;
  height: 150px;
  margin-left: 55px;
  position: relative;
  top: 40px;
}
.phone_image {
  width: 75px;
  height: 103px;
  animation: phone_run 0.5s;
  margin-left: 10px;
}
.phone_des {
  width: 75px;
  margin-left: 10px;
}
.phone_des span {
  font-size: 12px;
  color: #333333;
  display: block;
  text-align: center;
  margin-top: 5px;
}
.divider {
  display: block;
  width: 1px;
  height: 100px;
  background: #e0e0e0;
  position: absolute;
  top: 0px;
  left: 160px;
  animation: phone_run 0.5s;
}
@keyframes phone_run {
  from {
    height: 0;
  }
  to {
    height: 103px;
  }
}
@keyframes run {
  from {
    height: 0;
  }
  to {
    height: 230px;
  }
}
</style>



