<template>
  <div class="container">
    <div class="inner_container">
      <div class="banner" @mouseenter="enter" @mouseleave="leave">
        <div class="slideshow">
          <span class="iconfont icon-return left" @click="pre"></span>
          <span class="iconfont icon-enter right" @click="next"></span>
          <transition-group tag="ul" name="image">
            <li
              v-for="(img, index) in imageList"
              v-show="index === imageindex"
              :key="img"
            >
              <img :src="img" />
            </li>
          </transition-group>
        </div>
      </div>
      <div class="left-menu">
        <div class="mask"></div>
        <ul>
          <li @mouseenter="h_enter" @mouseleave="h_leave">
            <span>手机 电话卡</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
          <li>
            <span>电视 盒子</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
          <li>
            <span>笔记本 显示器</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
          <li>
            <span>家电 插线板</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
          <li>
            <span>出行 穿戴</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
          <li>
            <span>智能 路由器</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
          <li>
            <span>电源 配件</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
          <li>
            <span>健康 儿童</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
          <li>
            <span>耳机 音箱</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
          <li>
            <span>生活 箱包</span>
            <i class="iconfont icon-arrow-double-right"></i>
          </li>
        </ul>
      </div>
      <div
        class="hover_box"
        v-show="isture"
        @mouseenter="h_enter"
        @mouseleave="h_leave"
      >
        <div class="hover_inner">
          <ul class="list_1" v-for="(u, index) in 4" :key="index">
            <li class="list_1_1" v-for="(i, index) in 6" :key="index">
              <div>
                <img src="../assets/img/phone.png" alt="" />
                <span>小米10</span>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isture: false,
      imageindex: 0,
      timer: "",
      imageList: [
        require("../assets/img/banner.jpg"),
        require("../assets/img/banner1.jpg"),
        require("../assets/img/banner2.jpg")
      ]
    };
  },
  created() {
    this.leave();
  },
  methods: {
    play() {
      this.imageindex = this.imageindex + 1;
      if (this.imageindex == 3) {
        this.imageindex = 0;
      }
    },
    leave() {
      this.timer = setInterval(this.play, 8000);
    },
    enter() {
      clearInterval(this.timer);
    },
    pre() {
      this.imageindex = this.imageindex - 1;
      if (this.imageindex < 0) {
        this.imageindex = 2;
      }
      console.log(this.imageindex);
    },
    next() {
      this.imageindex = this.imageindex + 1;
      if (this.imageindex > 2) {
        this.imageindex = 0;
      }
      console.log(this.imageindex);
    },
    h_enter() {
      this.isture = true;
    },
    h_leave() {
      this.isture = false;
    }
  }
};
</script>

<style scoped>
.inner_container {
  margin-top: 24px;
  position: relative;
}
.banner {
  width: 1215px;
  height: 460px;
  cursor: pointer;
  overflow: hidden;
  position: relative;
}
.slideshow {
  width: 1215px;
  height: 460px;
  position: relative;
}
.slideshow span {
  display: inline-block;
  width: 50px;
  height: 80px;
  position: absolute;
}
.slideshow .left {
  z-index: 100;
  font-size: 50px;
  line-height: 80px;
  margin-left: 235px;
  margin-top: 180px;
}
.slideshow .right {
  z-index: 100;
  font-size: 50px;
  line-height: 80px;
  margin-left: 1166px;
  margin-top: 180px;
}
.slideshow .right:hover {
  background: #757879;
  opacity: 0.8;
}
.slideshow .left:hover {
  background: #757879;
  opacity: 0.8;
}
.slideshow li {
  position: absolute;
}
.slideshow li img {
  width: 1227px;
  height: 460px;
}
.image-enter-active,
.image-leave-active {
  transition: all 0.5s;
}
.image-enter {
  transform: translateX(100%);
}
.image-enter-to {
  transform: translateX(0);
}
.image-leave {
  transform: translateX(0);
}
.image-leave-to {
  transform: translateX(-100%);
}

.left-menu {
  width: 235px;
  height: 460px;
  position: absolute;
  top: 0px;
  left: 0px;
}
.mask {
  width: 235px;
  height: 460px;
  position: absolute;
  top: 0;
  left: 0;
  background: #8c989e;
  opacity: 0.5;
  z-index: 0;
}
.left-menu ul {
  position: absolute;
  z-index: 1;
  padding: 20px 0;
}
.left-menu ul li {
  width: 235px;
  height: 42px;
  color: white;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  line-height: 42px;
}
.left-menu ul li:hover {
  background: #ff6700;
}
.left-menu ul li span {
  margin-left: 30px;
}
.left-menu ul li i {
  position: absolute;
  right: 25px;
}
.hover_box {
  width: 992px;
  height: 460px;
  background: white;
  z-index: 1000;
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 235px;
}
.hover_inner {
  width: 992px;
  height: 460px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.list_1 {
  width: 230px;
  height: 460px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.list_1_1 {
  width: 200px;
  height: 40px;
  margin-left: 20px;
}
.list_1_1 div {
  width: 200px;
  height: 40px;
}
.list_1_1 div span {
  display: inline-block;
  width: 20;
  height: 20;
  position: relative;
  bottom: 15px;
  left: 10px;
  font-size: 15px;
}
</style>



